









































































function getDate(type) {
	const date = new Date();

	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		year = year - 60;
	} else if (type === 'end') {
		year = year + 2;
	}
	month = month > 9 ? month : '0' + month;;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
}
export default {
	data() {
		return {
			title: 'picker',
			// 网点信息
			info: '',
			// 网点名字
			array: [],
			// 电池id
			id: '',
			index: 0,
			date: getDate({
				format: true
			}),
			startDate: getDate('start'),
			endDate: getDate('end'),
			time: '12:00',
			// 遮罩层数据
			maskStatus: false,
			num: 1,
		}
	},
	onLoad(option) {
		this.id = option.id
		// this.$store.state.dcId = option.id
		uni.setStorageSync('dcId',option.id)
		// console.log(this.$store.state.wdId,this.$store.state.dcId)
	},
	onShow() {
		var that = this;
		uni.request({
			url: that.http + 'order/getHnetList',
			header: that.header,
			method: 'POST',
			data: {
				dian_id: that.id
			},
			success: (result) => {
				uni.hideLoading()
				console.log(result)
				if (result.data.code == 200) {
					console.log(result.data.data)
					that.info = result.data.data
					for (var i of result.data.data.net_list) {
						that.array.push(i.name)
					}
				} else {
					uni.showToast({
						title: result.data.mes,
						icon: 'none'
					})
				}
			},
			fail() {
				uni.showToast({
					title: '请求网点信息失败，请刷新后重试!',
					icon: 'none'
				})
			}
		})
	},
	methods: {
		// 遮罩层确定和取消
		maskClick(e) {
			var that = this;
			// 存网点id
			var net_id = this.info.net_list[this.index].net_id
			// that.$store.state.wdId = net_id
			uni.setStorageSync('wdId',net_id)
			// 时间
			var yue_time = this.date + ' ' + this.time
			var id = e.currentTarget.dataset.id;
			if (id == 1) {
				// 确认支付
				that.maskStatus = !that.maskStatus;
				var arr = {
					type:3,		//换电池
					pay_type: 1, //余额支付
					money:this.info.huan_money,	//最后金额
					yue_time:yue_time	//预约时间
				}
				arr = JSON.stringify(arr)
				uni.navigateTo({
					url: '../pw/pw?arr=' + arr
				})
			} else if (id == 2) {
				that.maskStatus = !that.maskStatus;
			}
		},
		bindPickerChange: function(e) {
			// console.log('picker发送选择改变，携带值为：' + e.target.value)
			this.index = e.target.value
		},
		bindDateChange: function(e) {
			// console.log('日期' + e.target.value)
			this.date = e.target.value
		},
		bindTimeChange: function(e) {
			// console.log('时间' + e.target.value)
			this.time = e.target.value
		}
	}
}
